/* stylelint-disable */

@mixin for-desktop {
  @media (min-width: (1025px)) {
    @content;
  }
}

@mixin tablet {
  @media only screen and (max-width: (1200px)) {
    @content;
  }
}

@mixin small-tablet {
  @media only screen and (max-width: (768px)) {
    @content;
  }
}

@mixin mobile {
  @media only screen and (max-width: (600px)) {
    @content;
  }
}
